//****************************************
// hero
//****************************************
.section-hero {
  padding-bottom: 0;
  background: $color-accent;
  overflow: hidden;
  &--copy {
    grid-column: 2/6;
    align-self: center;
    padding: 100px 0 200px 0;
    position: relative;
    z-index: 1;

    .hero--jobTitle {
      @extend .font-serif;
      font-size: 1.2em;
      font-style: italic;
      text-transform: lowercase;
      padding-bottom: 1em;
    }

    h1 {
      margin-top: 0;
      margin-bottom: 0.3em;
      color: $color-contrast;
    }

    p {
      font-size: 1.2em;
      line-height: 1.5em;

      @include breakpoint(tablet) {
        font-size: 1.4em;
        line-height: 1.6em;
      }
    }
  }

  &--image {
    grid-column: 1/8;
    align-self: end;
    position: relative;

    .anim-type {
      max-width: 200px;
      height: auto;
      margin: 0 auto;

      svg {
        display: block;
      }

      @include breakpoint(talbet) {
        max-width: 250px;
        width: 100%;
      }
    }
  }

  @include breakpoint(tablet) {
    &--copy {
      grid-column: 2/6;
    }

    &--image {
      grid-column: 6/8;
    }
  }

  .shape {
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0.85;
    &--1 {
      bottom: 60%;
      right: 10%;
    }
    &--2 {
      bottom: 30%;
    }
    &--3 {
      bottom: 15%;
      right: 20%;
    }
    &--4 {
      bottom: -20%;
      right: 5%;
    }
    &--5 {
      bottom: 15%;
      right: 15%;
      transform: rotate(20deg);
      svg path {
        stroke: $color-base;
        stroke-width: 0.5px;
        fill: transparent;
      }
    }
  }
}
